	font-family: 'Ubuntu';
	src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
	src: local('Ubuntu-Regular'), local('Ubuntu-Medium'),
		url(${___CSS_LOADER_URL_REPLACEMENT_0___}) format('truetype');
	font-weight: normal;
	font-style: normal;
}

body {
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 50px;
    font-family: 'Ubuntu', sans-serif;
}

html,
body {
    min-height: 100vh;
}

.header {
    width: 100%;

    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 40px 15px 50px;

    background: linear-gradient(92.7deg, #4CBBC4 0%, #3cd3ad 100%), #232323;
}

.title {
    margin-bottom: 20px;
    font-family: 'Ubuntu';
    font-weight: 700;
    font-size: 44px;
    line-height: 52px;
    text-align: center;
    color: #ffffff;
    font-style: normal;
}

.form {
    display: flex;
    justify-content: space-between;
    width: 460px;
    height: 54px;
    background: #ffffff;
    border-radius: 6px;

}

.input {
    padding: 15px;
    flex-grow: 1;
    font-weight: 300;
    font-size: 20px;
    line-height: 1;
    color: #000000;
}

.btn {
    height: 54px;
    width: 145px;
    border-radius: 6px;
    background: #ef0303;


    font-weight: 500;
    font-size: 20px;
    line-height: 1;
    color: #ffffff;

    transition: background-color 0.2s ease-in;

}

.btn:hover {
    background: #ee3939;
}

.card {
    position: relative;
    display: flex;
    flex-direction: column;
    row-gap: 20px;
    padding: 30px;
    min-width: 360px;
    background-color: #ffffff;
    border-radius: 6px;
    box-shadow: 0px 5px 20px rgba(27, 108, 114, 0.25);

}

.card:before {
    position: absolute;
    left: 10px;
    bottom: -10px;
    z-index: -1;
    content: "";
    height: 100%;
    width: calc(100% - 20px);
    background-color: #ffffff;
    border-radius: 6px;
    box-shadow: 0px 5px 20px rgba(27, 108, 114, 0.25);

}

.card-city {
    font-family: 'Ubuntu';
    font-weight: 500;
    font-size: 28px;
    line-height: 33px;
    color: #000000;
}

.card-city span {
    display: inline-block;
    vertical-align: text-top;
    padding: 3px 5px;
    background: #e5464f;
    border-radius: 6px;
    color: #ffffff;
    font-weight: 500;
    font-size: 12px;
    line-height: 1;

}

.card-weather {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.card-value {
    font-family: 'Ubuntu';
    font-weight: 500;
    font-size: 72px;
    line-height: 1;

}

.card-value sup {
    font-size: 50%;
}

.card-img {
    width:159px; 
    height:159px;
    object-fit: contain;
}

.card-desc {
    font-size: 20px;
    line-height: 1.2;
}

.footer {
    width: 100%;
    padding: 8px 15px;

    font-size: 14px;
    line-height: 1;
    text-align: center;
    background: #e3f1ee;
    color: #2e7695;

}

.footer a {
    color: #2e7695;
    text-decoration: none;
}`, "",{"version":3,"sources":["webpack://./src/css/main.css"],"names":[],"mappings":"AAGA;CACC,qBAAqB;CACrB,4CAAuC;CACvC;4DACsD;CACtD,mBAAmB;CACnB,kBAAkB;AACnB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,aAAa;IACb,iCAAiC;AACrC;;AAEA;;IAEI,iBAAiB;AACrB;;AAEA;IACI,WAAW;;IAEX,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,uBAAuB;;IAEvB,uEAAuE;AAC3E;;AAEA;IACI,mBAAmB;IACnB,qBAAqB;IACrB,gBAAgB;IAChB,eAAe;IACf,iBAAiB;IACjB,kBAAkB;IAClB,cAAc;IACd,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,YAAY;IACZ,YAAY;IACZ,mBAAmB;IACnB,kBAAkB;;AAEtB;;AAEA;IACI,aAAa;IACb,YAAY;IACZ,gBAAgB;IAChB,eAAe;IACf,cAAc;IACd,cAAc;AAClB;;AAEA;IACI,YAAY;IACZ,YAAY;IACZ,kBAAkB;IAClB,mBAAmB;;;IAGnB,gBAAgB;IAChB,eAAe;IACf,cAAc;IACd,cAAc;;IAEd,yCAAyC;;AAE7C;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,kBAAkB;IAClB,aAAa;IACb,sBAAsB;IACtB,aAAa;IACb,aAAa;IACb,gBAAgB;IAChB,yBAAyB;IACzB,kBAAkB;IAClB,iDAAiD;;AAErD;;AAEA;IACI,kBAAkB;IAClB,UAAU;IACV,aAAa;IACb,WAAW;IACX,WAAW;IACX,YAAY;IACZ,wBAAwB;IACxB,yBAAyB;IACzB,kBAAkB;IAClB,iDAAiD;;AAErD;;AAEA;IACI,qBAAqB;IACrB,gBAAgB;IAChB,eAAe;IACf,iBAAiB;IACjB,cAAc;AAClB;;AAEA;IACI,qBAAqB;IACrB,wBAAwB;IACxB,gBAAgB;IAChB,mBAAmB;IACnB,kBAAkB;IAClB,cAAc;IACd,gBAAgB;IAChB,eAAe;IACf,cAAc;;AAElB;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;AACvB;;AAEA;IACI,qBAAqB;IACrB,gBAAgB;IAChB,eAAe;IACf,cAAc;;AAElB;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,mBAAmB;AACvB;;AAEA;IACI,eAAe;IACf,gBAAgB;AACpB;;AAEA;IACI,WAAW;IACX,iBAAiB;;IAEjB,eAAe;IACf,cAAc;IACd,kBAAkB;IAClB,mBAAmB;IACnB,cAAc;;AAElB;;AAEA;IACI,cAAc;IACd,qBAAqB;AACzB","sourcesContent":["@import \"./reset.css\";\r\n@import url(https://fonts.googleapis.com/css?family=Ubuntu:300,regular,500,700);\r\n\r\n@font-face {\r\n\tfont-family: 'Ubuntu';\r\n\tsrc: url('../fonts/Ubuntu-Regular.ttf');\r\n\tsrc: local('Ubuntu-Regular'), local('Ubuntu-Medium'),\r\n\t\turl('../fonts/Ubuntu-Regular.ttf') format('truetype');\r\n\tfont-weight: normal;\r\n\tfont-style: normal;\r\n}\r\n\r\nbody {\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-i